@import url('https://fonts.googleapis.com/css?family=Audiowide');

body {
    overflow-x: hidden;
    overflow-y: scroll;
    background-color: #0d1019;
    font-family: 'Audiowide';
}


.container-div {
    display: inline-flex;
    justify-content: space-evenly;
    align-items: center;
    width: 100vw;
    height: 66vh;
}

.container-div .content-box-div {
    width: 100%;
    padding: 3vh;
    padding-top: 0vh;
    height: 90%;
}

.news-div {
    height: auto;
    width: 100%;
    position: absolute;
    bottom: 0;
} 

.container-bottom-bar {
    display: inline-flex;
    justify-content: left;
    align-items: center;
    width: 100%;
    height: 20vh;
    background-color: #151B29;
    margin: 0;
    padding-bottom: 2vh;
    border-bottom: 1vh solid #222a37;
    overflow-y: auto;
}

.media-title {
    text-align: left;
    margin: 0;
    background-color: #222a37;
    color: #ffff;
    font-weight: 100;
    width: 100%;
    height: auto;
    border-bottom: .2vh solid #0984e3;
}

.art-line {
    width: 30vw;
    height: .1vh;
    background-color: red;
}

.media-title h3 {
    font-size: 2.5vh;
    margin: 1vh;
}

@media screen and  (max-width: 600px){
    body {
        overflow-y: auto;
    }

    .container-div {
        display: inline-block;
        width: 100%;
    }

    .container-bottom-bar {
        display: none;
    }

    .container-div .content-box-div {
        width: 90%;
        margin-right: 5%;
    }

    .media-title {
        display: none;
    }
}